.App {
  text-align: center;
  background-color: #121212;
  width: 100%;
  height: 100%;
  /* display: flex; */
  color: white;
}

body {
  padding: 0%;
  margin: 0;
  background-color: #121212;
}

nav {
  height: 8vh;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid grey;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  font-size: 30px;
}

.game {
  width: 100vw;
  height: 100%;
  display: flex;
  align-items: center;
  padding-top: 5px;
  flex-direction: column;
}

.gameOver {
  margin-top: 5vh;
  font-size: 20px;
  font-family: Helvetica, Arial, sans-serif;
}

.board {
  margin-top: 1vh;
  width: 50vh;
  height: 55vh;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
  margin: 5px;
}

.letter {
  flex: 33%;
  height: 100%;
  border: 1px solid grey;
  margin: 5px;
  display: grid;
  place-items: center;
  font-size: 30px;
  font-weight: bolder;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

#correct {
  background-color: #528d4e;
}

#almost {
  background-color: #b49f39;
}

#error {
  background-color: #3a393c;
}

.keyboard {
  width: 70vh;
  height: 37vh;
  margin-top: 2vh;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 5px;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  width: 6vh;
  height: 8vh;
  margin: 5px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  font-size: 20px;
  background-color: grey;
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 600;
  cursor: pointer;
}

#NaW {
  background-color: #bb4242;
}

#big {
  width: 60px;
  font-size: 12px;
}
#disabled {
  background-color: #3a393c;
}

@media (max-width: 400px) {

  .game {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding-top: 5px;
    flex-direction: column;
  }

  .gameOver {
    margin-top: 5vh;
    font-size: 20px;
    font-family: Helvetica, Arial, sans-serif;
  }

  .board {
    width: 40vh;
    height: 47vh;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
  }

  .letter {
    flex: 33%;
    height: 100%;
    border: 1px solid grey;
    margin: 5px;
    display: grid;
    place-items: center;
    font-size: 30px;
    font-weight: bolder;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
  }

  .keyboard {
    width: 45vh;
    height: 30vh;
    margin-top: 3vh;
  }

  .key {
    width: 6vh;
    height: 8vh;
    margin: 5px;
    border-radius: 4px;
    display: grid;
    place-items: center;
    font-size: 20px;
    background-color: grey;
    color: white;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 600;
    cursor: pointer;
  }
}